import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { AlertController, IonModal } from '@ionic/angular';
import { map } from 'rxjs/operators';
import { Subscription } from 'rxjs';
import { ICategory } from 'src/app/models/category.model';
import { CategoryService } from 'src/app/services/category.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { appUrl } from 'src/app/app-url.module';
import { Location } from '@angular/common';
import { GlobalService } from 'src/app/services/global.service';

@Component({
  selector: 'app-category',
  templateUrl: './category.page.html',
  styleUrls: ['./category.page.scss'],
  providers: [IonModal],
})
export class CategoryPage implements OnInit, OnDestroy {
  @ViewChild(IonModal) modal: IonModal;
  isCreate = false;
  isModal = false;
  loading = false;
  backdrop = false;
  categories: ICategory[] = [];
  url = appUrl;
  form = new FormGroup({
    id: new FormControl(''),
    name: new FormControl('', [Validators.required]),
  });

  private ondestroy$ = new Subscription();

  constructor(
    private cateService: CategoryService,
    private alertController: AlertController,
    private location: Location,
    private globalService: GlobalService
  ) {}

  get f() {
    return this.form.controls;
  }

  ngOnInit() {
    this.onFetchData();

    this.ondestroy$ = this.globalService.loading$.subscribe((value) => {
      this.backdrop = value;
    });
  }
  ngOnDestroy(): void {
    this.ondestroy$.unsubscribe();
  }

  isBack() {
    this.location.back();
  }

  setModal() {
    this.isModal = !this.isModal;
  }

  closeForm() {
    this.isCreate = false;
    this.form.reset();
    this.setModal();
  }

  isRefresh(evt: any) {
    setTimeout(() => {
      this.onFetchData();
      evt.target.complete();
    }, 500);
  }

  onFetchData() {
    this.globalService.loading$.next(true);
    this.cateService.getAllCategory().then((data) => {
      data.pipe(map((val) => val.cate)).subscribe({
        next: (value) => {
          this.categories = value;
        },
        error: () => {
          this.globalService.loading$.next(false);
        },
        complete: () => {
          this.globalService.loading$.next(false);
        },
      });
    });
  }

  onFilter(data: ICategory) {
    this.isCreate = true;
    this.form.setValue({
      id: data.id,
      name: data.name,
    });
    this.setModal();
  }

  onSave() {
    if (this.form.invalid) {
      return this.form.markAllAsTouched();
    }

    const category: any = {
      categoryId: this.f.id.value,
      name: this.f.name.value,
    };
    if (this.isCreate) {
      this.cateService.updateCategory(category).subscribe({
        next: () => {
          this.onFetchData();
          this.closeForm();
        },
        error: () => {},
        complete: () => {},
      });
    } else {
      this.cateService.createCategory(category).subscribe({
        next: () => {
          this.onFetchData();
          this.closeForm();
        },
        error: () => {},
        complete: () => {},
      });
    }
  }

  async onDelete(data: ICategory) {
    const alert = await this.alertController.create({
      header: 'ທ່ານຕ້ອງການລົບ ?',
      subHeader: data.name,
      buttons: [
        {
          text: 'ຍົກເລີກ',
          role: 'cancel',
          cssClass: 'alert-cancel',
          handler: () => {},
        },
        {
          text: 'ຕົກລົງ',
          role: 'confirm',
          cssClass: 'alert-confirm',
          handler: () => {
            this.cateService.deleteCategory(data.id).subscribe({
              next: () => {
                this.onFetchData();
              },
              error: () => {},
              complete: () => {},
            });
          },
        },
      ],
    });

    await alert.present();
  }
}
